import React from 'react';
import FormTmpl from './components/form';
import SuccessMsg from './components/succesMsg'

class Form extends React.Component {
     state = {
       view: 'form'

     }
           
  changeView = () => {

   this.setState({view: 'msg'})

  }  
    
  render() {
   
       
    return  <div>{this.state.view === 'msg' ? <SuccessMsg /> : <FormTmpl data={this.changeView} />}</div>
  }
}

export default  Form;
